<template>
<div class="form">
  <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
    <el-form-item label="姓名：">
      <el-input v-model="formLabelAlign.name"  placeholder="请输入姓名"></el-input>
    </el-form-item>
    <el-form-item label="身份证号码：" >
      <el-input v-model="formLabelAlign.number" placeholder="请输入身份证号码"></el-input>
    </el-form-item>
    <el-form-item label="民族：" >
      <el-input v-model="formLabelAlign.type" placeholder="请输入民族"></el-input>
    </el-form-item>
    <el-form-item label="公司名称：" >
      <el-input v-model="formLabelAlign.company" placeholder="请输入公司名称"></el-input>
    </el-form-item>
    <el-form-item label="参保时间：">
      <el-input v-model="formLabelAlign.time"  placeholder="请输入参保时间"></el-input>
    </el-form-item>
    <el-form-item class="button">
    <el-button   type="primary" @click="submitForm('ruleForm2')">下一步</el-button>
  </el-form-item>
  </el-form>
</div>
</template>
<script>
  export default {
    data() {
      return {
        labelPosition: 'right',
        formLabelAlign: {
          name: '',
          number: '',
          type: '',
          company:'',
          time:''
        }
      };
    },
    methods:{
      submitForm(){
        location.href = `cart.html`
      }
    }
  }
</script>

<style>
.form .el-form{
  margin: 0 auto;
}
.form .el-form-item__label{
  min-width: 120px;
}
.form .el-input{
  width: 80%

}
.form .button{
  margin: 0 auto !important; 
  width: 80%;
}
.form .button .el-form-item__content{
  margin: 0 auto !important;
}
.form .el-button{
  width: 100%;
}
</style>



